import { createRouter, createWebHashHistory } from 'vue-router'
import AppErrors from '../views/AppErrors.vue'

const routes = [
  { path: '/', name: 'StoreLocator', props: false, component: () => import('@/views/StoreLocator.vue') },
  { path: '/:storeId', name: 'StoreView', props: true, component: () => import('@/views/StoreView.vue') },
  { path: '/error/:code', name: 'AppErrors', props: true, component: AppErrors },
  { path: '/*', component: AppErrors }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
