import axios from 'axios';
import type { Business, Category, MenuItem } from "@/interfaces";
import { ActionTree, GetterTree, MutationTree} from "vuex";

const axiosOptions = { 
    withCredentials: false, 
    timeout: 1000
}

interface RootState {
    storeModule: StoreState;
}

export interface StoreState {
    business: Business;
    categories: Array<Category>;
    items: Array<MenuItem>;

    categoriesByID: Record<string, Category>;
    itemsByID: Record<string, MenuItem>;
    itemsByCategoryID: Record<string, Array<MenuItem>>;
}

const nullBusiness: Business = {
    id: "",
    default_lang: "en",
    name: "default name",
    logo_url: "", 
    hero_url: "", 
    menu_url: "",
    translations: ["en"],
    address: {
        street: "", 
        street2: "",
        phone: "",
        city: "", 
        region: "",
        country: "", 
        zip: "",
        lat: 0.0,
        lon: 0.0
    }, 
    social: {}
}

const state = (): StoreState => ({
    business: nullBusiness,
    categories: [],
    items: [],
    categoriesByID: {},
    itemsByID: {},
    itemsByCategoryID: {},
});

const getters: GetterTree<StoreState, RootState> = {
    fullStore: state => {
        console.log("GETTER", state.items.length)
        return {
            business: state.business,
            categories: state.categories, 
            categoriesByID: state.categoriesByID,
            items: state.items, 
            itemsByID: state.itemsByID,
            itemsByCategoryID: state.itemsByCategoryID,
        }
    }
}

const mutations: MutationTree<StoreState> = {
    SET_STORE_DATA(state, resp) {
        const data = resp.data

        if (data.business) {
            state.business = data.business
        }

        const categoriesByID: Record<string, Category> = {}
        if (data.categories) {
            state.categories = data.categories
            data.categories.forEach((category: Category) => {
                categoriesByID[category.id] = category;
            });
        }
        state.categoriesByID = categoriesByID

        if (data.items) {
            state.items = data.items
            const itemsByID: Record<string, MenuItem> = {}
            const itemsByCategoryID: Record<string, Array<MenuItem>> = {}
            data.items.forEach((item: MenuItem) => {
                const cat = categoriesByID[item.cat_id]
                if (!cat) {
                    console.log("[ERROR] Food item", item.id, "has invalid cat", item.cat_id)
                }
                itemsByID[item.id] = item;
                const itemsInCat = itemsByCategoryID[item.cat_id] ?? []
                itemsInCat.push(item)
                itemsByCategoryID[item.cat_id] = itemsInCat
            });
            state.itemsByID = itemsByID
            state.itemsByCategoryID = itemsByCategoryID
        }
    }
}

const actions: ActionTree<StoreState, RootState> = {

    async getStoreById({ commit }, storeId: string) {
      return axios.get(`https://hackemy.github.io/livemenu/data/${storeId}.json`, axiosOptions)
      .then(resp => {
            if (resp.data) {
                commit('SET_STORE_DATA', { data: resp.data })
            }
      })
      .catch((err) => {
          if (err.response && err.response.status === 400) {
              return
          }
          throw err // to ui catch
      });
  }

}

export default {
    namespaced: false,
    state,
    getters,
    actions,
    mutations
};

  